.layout .popper-fab {
    position: fixed;
    top: 160px;
    right: 0;
    border-radius: 20% 1px 1px 20%;
    z-index: 100; 
    background-color: #EEEEEE;
    opacity: .5;
}

.layout .popper-fab .icon {
    block-size: 1em;
    fill: #9f9f9f;
    vertical-align: middle;
}


.layout .popper-page {
    position: fixed;
    top: 165px;
    right: 60px;
    padding: 0px;
    background: #fafafa;
    z-index: 20;
    margin: 0px 0px 0px -10px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    border-left: 10px solid #424242;
    border-right: 10px solid #424242;
    border-top: 10px solid #616161;
    border-bottom: 10px solid #616161;
}

@media print {
    .layout .popper-fab {
        display: none;
    }
}
.layout {
    display: -webkit-flex;
    display: flex;
}

.layout-content {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    background: #FAFAFA;
}

.layout-page {
    width: 70%;
    margin: auto;
}

.layout-page-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.layout-page-title {
    color: #5f5f5f;
    text-decoration: none;
    font-family: Pacifico, cursive;
    font-size: 30px;
    font-weight: normal;
}

.layout-page-sub-title {
    margin: -5px 0px 5px 0px;
    font-size: 16px;
    color: #5E5E5E;
    font-weight: 400;
    line-height: 1.4em;
}

.layout-page-content {
    color: #5f5f5f;
}

.layout-page-footer {
    color: #5f5f5f;
}

@media print {
    .layout-page {
        width: 100%;
    }
}

@media screen and (max-width: 767px) {
    .layout-page {
        width: 100%;
    }
}
